	module.exports = {"data":{"post":{"id":"/Users/varya/WebDev/Personal/varya-gatsby/content/life/life-blog/index_ru.md absPath of file >>> MarkdownRemark","html":"<p>Больше двух лет я пыталась использовать Тумблер для личного блога. Но сегодня личный блог окончательно переехал\nсюда. Тумблер был не очень удобен, поэтому посты перенеслись легко — не так уж много их и накопилось за пару лет.</p>\n<p>Кроме того, часто были жалобы про “не могу на varya.me найти твой старый пост”. Неудивительно: все эти посты были вовсе\nне здесь. Теперь проблема решена.</p>\n<excerpt/>\n<p>Я думала, что буду писать в траспорте и в путешествиях. Поэтому очень нравилось, что у Тумблера есть приложения для\nтелефонов и планшетов. Но это не сработало. По тем или иным причинам было неудобно.</p>\n<p>Мне по-прежнему важно размещать любой контент, в том числе карты, и это отсекает практически все существующие\nблого-платформы. Но со standalone таких проблем нет.</p>\n<p>Единственный минус — всё открыто, и я не смогу использовать репозиторий для подготовки “сенсационных” постов. Впрочем,\nза двухлетнюю историю он такой был один :-) Про переезд в Финляндию.</p>\n<p>Вообще, можете почитать старые посты — они все в разделе <a href=\"/ru/life\">про жизнь</a>. Я перечитала некоторые, бывает очень\nмило и наивно :-)</p>\n<p>Комменты перенеслись без проблем, все ваши ценные мнения сохранены!</p>\n<p>Из плюсов — будет легче поддерживать единый дизайн технической и нетехнической части блога. Исходники открыты, так что\nвы сможете отправлять мне пул-реквесты с правкой ошибок. У меня теперь есть возможность вести англоязычный блог. Пока\nтам пусто, но я намерена строить нерусскоязычный социальный граф. Надеюсь, это поможет.</p>\n<p>Для тех, кто раньше читал через Тумблер, будут зеркальные посты — не переживайте. RSS пока нету. Но будет.</p>","fields":{"slug":"ru/life/life-blog/","prefix":"","disqusIdentifier":"life-life-blog-index-ru"},"frontmatter":{"title":"Персональный блог, теперь standalone","date":"25 August 2015","v2":true,"old":null,"tumblr":null}},"authornote":{"id":"/Users/varya/WebDev/Personal/varya-gatsby/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mr. Gatsby</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/\">bibendum</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. Blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus.</p>"},"site":{"siteMetadata":{"siteUrl":"http://varya.me/"}}},"pathContext":{"slug":"ru/life/life-blog/","prev":{"id":"/Users/varya/WebDev/Personal/varya-gatsby/content/life/life-in-finland-10/index_en.md absPath of file >>> MarkdownRemark","fields":{"slug":"en/life/life-in-finland-10/","prefix":"","lang":"en","disqusIdentifier":"life-life-in-finland-10-index-en"},"frontmatter":{"title":"Finland - driverless busses, free-to-use public laptops, buying drugs in pharmacy"}},"next":{"id":"/Users/varya/WebDev/Personal/varya-gatsby/content/life/home-finance/index_ru.md absPath of file >>> MarkdownRemark","fields":{"slug":"ru/life/home-finance/","prefix":"","lang":"ru","disqusIdentifier":"life-home-finance-index-ru"},"frontmatter":{"title":"Как мы копим и тратим свои деньги"}}}}

